@use "@utils/variables" as v;

.wrapper {
    position: relative;

    .graphic {
        img {
            width: 100%;
            height: 100%;
        }
    }

    .absoluteContent {
        position: absolute;
        top: 0;
        left: 0;
    }

    .heroSection {
        position: relative;
        margin-bottom: 3em;
        background-image: url("../../assets/home/hero.svg");
        min-height: 45em;
        background-position: center;
        background-size: cover;

        overflow-y: hidden;

        .graphic.hero {
            width: 100%;
            img {
                height: 100%;
            }
        }

        .graphic.star {
            img {
                width: 18px;
            }

            position: absolute;

            &.top {
                left: 13%;
                top: 2em;
            }

            &.bottomLeft {
                bottom: 7em;
                left: 32%;
            }

            &.bottomRight {
                bottom: 16em;
                right: 28em;
            }
            &.topRight {
                top: 12em;
                right: 21em;
            }
            &.topLeft {
                left: 28%;
                top: 20em;
            }
        }

        .graphic.wave {
            position: absolute;

            top: 140px;
            right: 0%;
            z-index: -1;

            img {
                width: 700px;
            }
        }

        .graphic.redMoon {
            position: absolute;
            top: 250px;
            right: 12%;
            img {
                width: 40px;
            }
        }

        .graphic.heroBoy {
            position: absolute;
            left: 40%;
            top: 140px;

            img {
                max-width: 280px;
            }

            .lottie.butterflyBlueYellow {
                position: absolute;
                bottom: 0;
                left: -100px;
                transform: rotate(25deg);
            }

            .lottie.shiningStars {
                position: absolute;
                width: 100%;
                top: 70%;
                transform: translateY(-50%);
            }
        }

        .graphic.bug {
            position: absolute;
            bottom: 220px;
            left: 4%;
            img {
                width: 40px;
            }
        }

        .graphic.shootingStar {
            position: absolute;
            bottom: 100px;
            left: 14%;
            img {
                width: 90px;
            }
        }

        .lottie.jupiter {
            position: absolute;
            top: 90px;
            right: 28%;
        }

        .lottie.clouds {
            position: absolute;
            top: -180px;
            left: 12%;
            z-index: -1;
        }

        header {
            padding-top: 10em;
            margin-bottom: 2.5em;
            width: fit-content;
            position: relative;

            .lottie.butterflySideview {
                position: absolute;
                top: 3.9em;
                right: 5.5em;
                transform: rotate(-30deg);
            }

            h1 {
                margin-bottom: 0.5em;
                max-width: 550px;
                font-size: 2.5em;
                white-space: nowrap;
                overflow-x: auto;

                div:last-child {
                    font-size: 3rem;
                }
            }

            h3 {
                font-weight: 400;
            }
        }

        button {
            font-size: 1.2rem;
            padding-inline: 2rem;
            transition: all 0.8s;

            $btnHoverColor: #c456ae;
            &:hover {
                background-color: $btnHoverColor;
                transform: scale(1.05);
            }
        }
    }

    .whoSection {
        position: relative;
        background-position: center;
        background-size: cover;
        background-image: url("../../assets/home/who_clouds.svg");
        padding-bottom: 15em;
        margin-bottom: 0px;

        .graphic.whoClouds {
            width: 100%;
        }

        .graphic.numbers {
            position: absolute;
            right: 9em;
            top: 0;

            display: flex;
            flex-direction: column;

            img {
                width: 60px;
            }

            img.number1 {
                margin-bottom: 5em;
            }
            img.number2 {
                margin-bottom: 6em;
                margin-left: -4em;
                animation: rotation 3s ease-in infinite;
                animation-delay: 2s;
            }
            img.number0 {
                animation: rotation 3s ease-in infinite;
                animation-delay: 4s;
            }
        }

        .letters {
            position: absolute;
            bottom: 20px;
            right: 30em;
            width: fit-content;
            z-index: 2;

            display: flex;

            img.letterE {
                width: 80px;
                margin-top: 100%;
            }

            .lottie.letterA {
                margin-inline: 4em;
                left: 0;
                bottom: 0;
                height: 40px;
                margin-top: 80%;
            }

            img.letterS {
                width: 80px;

                margin-top: 100%;
            }
        }

        @keyframes rotation {
            0%,
            10% {
                -webkit-transform: rotateY(0);
            }
            90%,
            100% {
                -webkit-transform: rotateY(360deg);
            }
        }

        .lottie.rotatingEarth {
            position: absolute;
            right: 10%;
            bottom: 0%;
        }

        header {
            margin-bottom: 2em;
        }
        p {
            width: 85%;
            line-height: 35px;
            margin-bottom: 3em;
        }
    }

    .ourCourses {
        padding-top: 5em;
        padding-bottom: 5em;

        background-color: #e4f1fe;
        position: relative;

        header {
            margin-bottom: 2em;
        }

        .graphic {
            position: absolute;
            width: 100px;
            z-index: 2;

            img {
                width: 100%;
            }
        }
        .lottie {
            position: absolute;
            z-index: 2;
        }
        .lottie.rocket {
            bottom: 4em;
            left: 5em;
        }
        .lottie.sloth {
            bottom: 120px;
            left: 40%;
        }
        .lottie.tiger {
            right: 100px;
            width: 90px;
            bottom: 120px;
        }

        .graphic.planet {
            left: 26em;
            width: 180px;
        }
        .graphic.invertedPyramid {
            right: 25em;
            top: 8em;
        }
        .graphic.pyramid {
            bottom: 2em;
            left: 55%;
            width: 120px;
        }
        .graphic.ring {
            right: 6em;
            width: 90px;
        }
    }

    .featuresSection {
        padding-top: 5em;
        background-image: url("../../assets/home/features_bg.svg");
        background-size: cover;
        background-position: center;

        header {
            margin-bottom: 3.5em;
        }

        .features {
            $padd: 50px;
            article {
                display: flex;
                align-items: center;

                max-width: 70%;
                margin-bottom: 8em;

                .image {
                    width: 30%;
                    display: flex;
                    align-items: center;

                    img {
                        width: 100%;
                        height: 100%;
                        max-width: 250px;
                        max-height: 250px;
                        border-radius: 50%;
                        object-fit: cover;
                    }
                }

                .content {
                    width: 70%;
                    padding-left: $padd;

                    .title {
                        color: v.$themePurple;
                        margin-bottom: 1em;
                        font-size: 1.5em;
                        font-weight: 500;
                    }

                    .body {
                        line-height: 30px;
                    }
                }
            }

            article.alt {
                margin-left: auto;

                .image {
                    order: 2;
                }
                .content {
                    padding-left: 0;
                    padding-right: $padd;
                }
            }
        }
    }

    .testimonialsSection {
        position: relative;
        margin-block: 5em;
        margin-block-end: 0;

        header {
            margin-bottom: 3em;
        }

        footer {
            margin-top: 8em;
            padding-left: 400px;
            padding-bottom: 80px;

            button {
                width: 200px;
                height: 40px;
                font-size: 1.1rem;
                transition: all 0.4s;

                &:hover {
                    border: 1px solid v.$themePurple;
                    color: v.$themePurple;
                    background-color: white;
                }
            }
        }

        .graphic {
            position: absolute;
            bottom: -10px;
            left: 0;
            max-width: 800px;

            z-index: -1;

            img {
                width: 100%;
            }
        }

        .withBg {
            background-image: url("../../assets/home/testimonials.svg");
            background-position: center;
            background-size: cover;

            width: 100%;
            height: 75%;

            position: absolute;
            top: 0;
            left: 0;
        }

        .lottie {
            position: absolute;
            bottom: -20px;
            right: 20%;
        }
    }
}

@media screen and(max-width:1200px) {
    .wrapper {
        .heroSection {
            .graphic.heroBoy {
                left: unset;
                right: 10% !important;
                top: 140px;

                img {
                    width: 220px;
                }
            }

            .graphic.redMoon {
                display: none;
            }

            .graphic.wave {
                img {
                    width: 500px;
                }
            }

            .lottie.jupiter {
                right: 11%;
                display: none;
            }

            .graphic.shootingStar {
                display: none;
            }
        }

        .whoSection {
            .lottie.rotatingEarth {
                bottom: 0;
                right: 5%;
            }

            .graphic.numbers {
                right: 1%;
                display: none;
            }

            .graphic.letters {
                left: 10%;
                display: none;
            }
        }

        .testimonialsSection {
            footer {
                margin-top: 60px !important;
                padding-left: 0px !important;
                padding-bottom: 80px;
                display: flex;
                justify-content: center;

                button {
                    width: 150px;
                    height: 40px;
                }
            }
            .lottie {
                display: none;
            }
        }
    }
}

@media screen and (max-width: 1000px) {
    .wrapper {
        .featuresSection {
            .features {
                article {
                    max-width: 100%;

                    .image {
                        width: 25%;
                    }

                    .content {
                        width: 75%;
                    }
                }
            }
        }

        .testimonialsSection {
            footer {
                padding-left: 100px;
                padding-bottom: 80px;
            }
        }

        .whoSection {
            padding-bottom: 10em;

            .letters {
                display: none;
            }
        }
    }
}

@media screen and (max-width: 900px) {
    .wrapper {
        .heroSection {
            header {
                h1 {
                    font-size: 2.8rem;
                }
            }

            .graphic.hero {
                width: 100%;
                height: 600px;

                img {
                    display: block;
                    width: 100%;
                    height: 100%;
                }
            }
        }

        .whoSection {
            .graphic.letters {
                display: none;
            }
            .lottie.rotatingEarth {
                bottom: 0;
                right: 0%;
            }
        }

        .testimonialsSection {
            .graphic {
                width: 90%;
            }
        }
    }
}

@media screen and (max-width: 800px) {
    .wrapper {
        .heroSection {
            .graphic.wave {
                top: 300px;
                img {
                    width: 900px;
                }
            }

            .graphic.star {
                display: none;
            }

            header {
                padding-top: 10em;
                margin-bottom: 3.5em;

                h3 {
                    width: 60%;
                }
            }

            .graphic.heroBoy {
                top: 190px;
                left: unset;
                right: 100px;
                .lottie.butterflyBlueYellow {
                    display: none;
                }
            }
        }
    }
}

@media screen and (max-width: 800px) {
    .wrapper {
        .ourCourses {
            .lottie,
            .graphic {
                display: none;
            }
        }
    }
}
@media screen and (max-width: 600px) {
    .wrapper {
        overflow-x: hidden;

        .heroSection {
            .graphic.star {
                display: none;
            }

            .lottie.clouds {
                top: -80px;
                left: 3%;
            }

            .graphic.heroBoy {
                top: 250px;
                left: unset;
                right: 110px !important;

                img {
                    width: 170px;
                }
            }

            .graphic.heroBoy {
                top: 260px;
                right: 20px !important;

                img {
                    width: 140px;
                }
            }

            header {
                margin-bottom: 1.2em;

                h1 {
                    margin-bottom: 0.5em;
                    font-size: 2.2rem;
                }
                h3 {
                    font-weight: 400;
                    width: 65%;
                }
            }
        }

        .whoSection {
            .lottie.rotatingEarth {
                bottom: 0;
                right: 50%;
                transform: translateX(50%);
            }
        }
        .featuresSection {
            padding-top: 5em;
            padding-bottom: 2em;
            background-image: url("../../assets/home/features_bg.svg");
            background-size: cover;
            background-position: center;

            header {
                margin-bottom: 3.5em;
            }

            .features {
                article {
                    display: flex;
                    flex-direction: column;
                    align-items: unset;
                    justify-content: center;

                    max-width: 100%;
                    margin-bottom: 5em;

                    .image {
                        width: 100% !important;
                        display: flex;
                        align-items: center;
                        justify-content: center;
                        margin-bottom: 1.5em;

                        img {
                            width: 180px;
                            height: 180px;
                        }
                    }

                    .content {
                        width: 100%;
                        display: flex;
                        flex-direction: column;
                        align-items: center;
                        padding-inline: 0 !important;

                        .title {
                            text-align: center;
                        }

                        .body {
                            text-align: center;
                            padding: 0;
                        }
                    }
                }

                article.alt {
                    margin-left: auto;
                    .image {
                        order: 1 !important;
                    }

                    .content {
                        order: 2;
                    }
                }
            }
        }
    }
}

@media screen and (max-width: 450px) {
    .wrapper {
        .heroSection {
            header {
                h1 {
                    margin-bottom: 0.5em;

                    div:first-child {
                        font-size: 2rem;
                    }

                    div:last-child {
                        font-size: 2.5rem;
                    }
                }
            }
        }
    }
}
