@use "@utils/variables" as v;

.wrapper {
    width: 100%;
    padding-bottom: 3em;

    .slideWrapper {
        border: 2px solid black;
    }

    .slide {
        border-radius: 100px;
        background-color: white;
        height: 280px;
        padding: 3em;

        display: flex;
        flex-direction: column;
        align-items: center;

        cursor: pointer;

        box-shadow: 1px 1px 25px #5f5f5f;

        .name {
            color: v.$themePurple;
            font-size: 1.5rem;
            font-weight: 500;
            margin-bottom: 0.5em;
        }
        .desc {
            line-height: 32px;
            overflow: hidden;
            text-overflow: ellipsis;
            max-height: 70%;
            color: #888;
        }
    }

    .slide.active {
        box-shadow: 1px 1px 20px #9f0b91;
    }
}

@media screen and (max-width: 600px) {
    .wrapper {
        .slide {
            padding: 2em;
            border-radius: 80px;
        }
    }
}
