.wrapper {
    padding-top: 20px;
    padding-bottom: 5em;

    height: 520px;

    .slide {
        width: 100%;
        height: 100%;
        cursor: pointer;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        background-color: white;
        padding: 2em;
        border-radius: 50px;
        box-shadow: 1px 1px 10px rgba(1, 1, 1, 0.1);

        img {
            min-width: 90px;
            min-height: 90px;
            max-width: 90px;
            max-height: 90px;
            border-radius: 50%;
            margin-bottom: 15px;
            object-fit: cover;
        }

        .content {
            line-height: 30px;
        }
    }

    .slide.active {
        box-shadow: 1px 1px 20px #9f0b91;
    }
}

@media screen and (max-width: 600px) {
    .wrapper {
        height: 600px;
    }
}
